<template>
  <div class="step-wrap step5-css" v-loading="fullscreenLoading">

    <el-tabs class="el-tabs-css" v-model="currentTabName" :closable="closable" v-if="editableTabsList.length>0">
      <el-tab-pane
        v-for="item in editableTabsList"
        :key="item.sort"
        :label="item.title"
      >
      <div class="step-body-wrap step-body-wrap-step5">
        <el-form ref="form" :model="item.contentForm">
            <div class="h3-title">处理意见</div>
          <el-row class="block-wrap">
            <el-col v-for="radioItem in item.contentForm.suggestionRadioListPre" :key="radioItem.label" :span="6" class="item-wrap">
              <el-radio @change="getNewRadio(item)" v-model="item.contentForm.suggestionRadioVal" :label="radioItem.label" :title="radioItem.labelName">{{radioItem.labelName}}</el-radio>
            </el-col>
          </el-row>

            <div class="h3-title" v-show="item.contentForm.suggestionCheckListPre.length>0">处理意见依据</div>
          <el-row class="block-wrap step-5-checkbox" v-show="item.contentForm.suggestionCheckListPre.length>0">
            <el-checkbox-group v-model="item.contentForm.suggestionCheckList">
            <el-col v-for="checkboxItem in item.contentForm.suggestionCheckListPre" :key="checkboxItem" :span="12" class="item-wrap">
              <el-checkbox :title="checkboxItem" v-model="item.contentForm.suggestionRadioVal" :label="checkboxItem" :key="checkboxItem" @change="checked=>checkRow(checked, item)"></el-checkbox>
            </el-col>
            </el-checkbox-group>
          </el-row>

          <div class="h3-title generatingConclusion-title">
            <span class="generatingConclusion-text">最终结论</span>
            <!-- <br/> -->
            <!-- <span class="generatingConclusion"><el-button size="small" :disabled="!item.contentForm.choosedGistIds" @click="generatingConclusion(item)">生成结论</el-button></span> -->
          </div>
          <el-row class="block-wrap">
          <el-form-item label="">
            <el-input type="textarea" maxlength="2048" rows="10" v-model="item.contentForm.basicInfo" @change="changeBasicInfo"></el-input>
          </el-form-item>
        </el-row>

        </el-form>
      </div>
      </el-tab-pane>
    </el-tabs>
    <el-button v-if="editableTabsList.length>0"  class="addBtn" size="small" type="primary" @click="saveProcessingOpinion('isMergingProcessing')" :disabled="editableTabsList.length<=1">合并处理</el-button>
    <div class="step-footer-wrap">
      <el-button size="small" v-if="editableTabsList.length>0" @click="saveProcessingOpinion('')">保存</el-button>
      <el-button size="small" @click="prev">上一步</el-button>
      <el-button size="small" type="primary" @click="next()">下一步</el-button>
    </div>
    <div v-if="editableTabsList.length===0" class="step5-no-result">
            <div class="step5-no-cell">
              <img src="../../../assets/img/no_result.png" alt="">
              <p style="text-align: center;">暂无数据！</p>
            </div>
    </div>
      <!--合并处理弹出框 begin-->
  <el-dialog
  :visible.sync="dialogVisible"
  width="580px"
  center
  :before-close="handleClose">
  <div class="yofc-dialog-header" slot="title">
    <h1>合并处理</h1>
  </div>
  <div class="yofc-transfer" v-if="dialogVisible">
  <el-transfer v-model="transferVal" :titles="['可合并处理的嫌疑人', '已选择']" :data="transferListData"></el-transfer>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button size="small" @click="dialogVisible = false,transferVal = []">取 消</el-button>
    <el-button size="small" type="primary" :disabled="transferVal.length<1" @click="getMergingProcessing()">确 定</el-button>
  </span>
</el-dialog>
  <!--合并处理弹出框 end-->
  </div>

</template>

<script>
import {
  getProcessingOpinion,
  getAllProcessingOpinionOptions,
  getGistByOptionId,
  generateConclusion,
  saveProcessingOpinion,
  mergingProcessing
} from '@/api/root'
export default {
  props: {
    caseObj: {
      type: Object,
      required: true
    }
  },
  name: 'step5',
  data() {
    return {
      msg: 'step5',
      tabIndex: 1,
      isSave: false,
      // 是否可以关闭tab
      closable: false,
      fullscreenLoading: false,
      currentTabName: '0',
      // labelPosition: 'top',
      // 弹窗相关 begin
      dialogVisible: false,
      // 弹窗相关 end

      // 穿梭框数据 begin
      transferVal: [],
      transferListData: [],
      // 穿梭框数据 end
      editableTabsList: [],
      suggestionRadioListPre: [],
      originEditableTabsList: []
    }
  },
  mounted() {
    this.loadData()
    window.checkIsSave = (path, close) => {
      this.next(path, close)
    }
  },
  methods: {
    loadData() {
      this.fullscreenLoading = true
      getAllProcessingOpinionOptions().then(res => {
        if (res.data.code === '200') {
          this.fullscreenLoading = false
          this.suggestionRadioListPre = []
          for (let item of res.data.data) {
            this.suggestionRadioListPre.push({
              label: item.optionId,
              labelName: item.optionContent
            })
          }
          this.getProcessingOpinion()
        } else {
          this.fullscreenLoading = false
          this.$message({
            type: 'warning',
            message: res.data.msg
          })
        }
      })
    },
    getProcessingOpinion() {
      this.fullscreenLoading = true
      getProcessingOpinion({ caseNumber: this.caseObj.caseNumber }).then(
        res => {
          if (res.data.code === '200') {
            this.fullscreenLoading = false
            this.editableTabsList = []
            this.originEditableTabsList = res.data.data
            res.data.data &&
              res.data.data.forEach((item, index) => {
                this.getGistByOptionId(item, index)
              })
          } else {
            this.fullscreenLoading = false
            this.$message({
              type: 'warning',
              message: res.data.msg
            })
          }
        }
      )
    },
    getNewRadio(item) {
      this.isSave = true
      item.contentForm.choosedGistIds = ''
      item.contentForm.suggestionCheckList = []
      this.fullscreenLoading = true
      getGistByOptionId({ optionId: item.contentForm.suggestionRadioVal }).then(
        res => {
          if (res.data.code === '200') {
            this.fullscreenLoading = false
            item.contentForm.suggestionCheckListPreOrigin = res.data.data
            item.contentForm.suggestionCheckListPre =
              res.data.data.map(row => row.gistContent) || ''
          } else {
            this.fullscreenLoading = false
            this.$message({
              type: 'warning',
              message: res.data.msg
            })
          }
        }
      )
      this.fullscreenLoading = true
      generateConclusion({
        optionId: item.contentForm.suggestionRadioVal,
        gistIds: '',
        suspectInfoId: item.suspectId,
        caseNumber: this.caseObj.caseNumber
      }).then(res => {
        if (res.data.code === '200') {
          this.fullscreenLoading = false
          item.contentForm.basicInfo = res.data.data
        } else {
          this.fullscreenLoading = false
          this.$message({
            type: 'warning',
            message: res.data.msg
          })
        }
      })
    },
    checkRow(checked, item) {
      this.isSave = true
      let bSet = new Set(item.contentForm.suggestionCheckList)
      let gistIds = Array.from(
        new Set(
          item.contentForm.suggestionCheckListPreOrigin.filter(v =>
            bSet.has(v.gistContent)
          )
        )
      )
        .map(row => row.gistId)
        .join(',')
      item.contentForm.choosedGistIds = gistIds
      this.generatingConclusion(item)
    },
    generatingConclusion(item) {
      // if (!item.contentForm.choosedGistIds) {
      //   this.$message({
      //     type: 'warning',
      //     message: '请选择处理依据'
      //   })
      //   return false
      // }
      this.isSave = true
      this.fullscreenLoading = true
      generateConclusion({
        optionId: item.contentForm.suggestionRadioVal,
        gistIds: item.contentForm.choosedGistIds,
        suspectInfoId: item.suspectId,
        caseNumber: this.caseObj.caseNumber,
        details: item.contentForm.basicInfo
      }).then(res => {
        if (res.data.code === '200') {
          this.fullscreenLoading = false
          if (!res.data.data) {
            this.$message({
              type: 'warning',
              message: '未能生成结论,请手动编辑'
            })
            return false
          }
          item.contentForm.basicInfo = res.data.data
          this.$message({
            type: 'success',
            message: '生成结论成功'
          })
        } else {
          this.fullscreenLoading = false
          this.$message({
            type: 'warning',
            message: res.data.msg
          })
        }
      })
    },
    saveProcessingOpinion(isMergingProcessing, path, close) {
      this.isSave = false
      let datas = []
      for (let item of this.editableTabsList) {
        datas.push({
          caseNumber: this.caseObj.caseNumber,
          choosedGistIds: item.contentForm.choosedGistIds,
          details: item.contentForm.basicInfo,
          opinionId: item.opinionId,
          optionId: item.contentForm.suggestionRadioVal,
          sort: item.sort,
          suspectName: item.suspectName,
          suspectId: item.suspectId,
          createTime: item.createTime,
          updateTime: item.updateTime,
          updateUser: item.updateUser,
          createUser: localCache.getItem('userId')
        })
      }
      saveProcessingOpinion(datas).then(res => {
        if (res.data.code === '200') {
          if (isMergingProcessing === 'isMergingProcessing') {
            this.$message({
              type: 'success',
              message: `自动保存成功,可进行合并处理`
            })
            this.mergingProcessing()
          } else {
            this.$message({
              type: 'success',
              message: `保存成功`
            })
          }
          if (close) {
            if (eDossier) {
              eDossier.close()
            }
          } else {
            if (path) {
              this.$router.push({ path: path })
            }
          }
        } else {
          this.$message({
            type: 'warning',
            message: res.data.msg
          })
        }
      })
    },
    getGistByOptionId(item, index) {
      let optionId = item.optionId || this.suggestionRadioListPre[0].label
      getGistByOptionId({ optionId: optionId }).then(res => {
        if (res.data.code === '200') {
          this.generateConclusion(res.data.data, item, index, optionId)
        } else {
          this.$message({
            type: 'warning',
            message: res.data.msg
          })
        }
      })
    },
    generateConclusion(datas, item, index, optionId) {
      generateConclusion({
        optionId: optionId,
        gistIds: item.choosedGistIds,
        suspectInfoId: item.suspectId,
        caseNumber: this.caseObj.caseNumber
      }).then(respon => {
        let intersection = []
        if (item.choosedGistIds) {
          let choosedGistIds = item.choosedGistIds.split(',')
          let bSet = new Set(choosedGistIds)
          intersection = Array.from(
            new Set(datas.filter(v => bSet.has(v.gistId + '')))
          ).map(row => row.gistContent)
        }
        let flag = true
        this.editableTabsList.forEach((value, key) => {
          if (value.suspectId === item.suspectId) {
            flag = false
            this.editableTabsList[key].contentForm = {
              suggestionRadioVal: item.optionId,
              suggestionRadioListPre: this.suggestionRadioListPre,
              suggestionCheckListPreOrigin: datas,
              suggestionCheckListPre: datas.map(row => row.gistContent) || '',
              choosedGistIds: item.choosedGistIds,
              suggestionCheckList: intersection || [],
              basicInfo: item.details
            }
          }
        })
        if (flag) {
          this.editableTabsList.push({
            title: item.suspectName,
            name: item.suspectName,
            suspectId: item.suspectId,
            opinionId: item.opinionId,
            suspectName: item.suspectName,
            createTime: item.createTime,
            updateTime: item.updateTime,
            updateUser: item.updateUser,
            sort: item.sort,
            contentForm: {
              suggestionRadioVal:
                item.optionId || this.suggestionRadioListPre[0].label,
              suggestionRadioListPre: this.suggestionRadioListPre,
              suggestionCheckListPreOrigin: datas,
              suggestionCheckListPre: datas.map(row => row.gistContent) || '',
              choosedGistIds: item.choosedGistIds,
              suggestionCheckList: intersection || [],
              basicInfo: item.details ? item.details : respon.data.data
            }
          })
          this.editableTabsList.sort(this.compare)
        }
      })
    },
    compare(obj1, obj2) {
      let val1 = obj1.sort
      let val2 = obj2.sort
      if (val1 < val2) {
        return -1
      } else if (val1 > val2) {
        return 1
      } else {
        return 0
      }
    },
    next(path, close) {
      this.isSaves('step6', path, close)
    },
    isSaves(step, path, close) {
      if (!this.isSave) {
        if (close) {
          if (eDossier) {
            eDossier.close()
          }
        } else {
          if (path) {
            this.$router.push({ path: path })
          } else {
            this.$router.push({ name: step })
          }
        }
      } else {
        this.$confirm('有修改的嫌疑人信息未保存, 是否保存?', '', {
          confirmButtonText: '保存',
          cancelButtonText: '取消',
          center: true,
          closeOnClickModal: false,
          showClose: false
        })
          .then(() => {
            this.saveProcessingOpinion('', path, close)
          })
          .catch(res => {
            this.$message({
              type: 'info',
              message: `已取消保存`
            })
            if (close) {
              if (eDossier) {
                eDossier.close()
              }
            } else {
              if (path) {
                this.$router.push({ path: path })
              } else {
                this.$router.push({ name: step })
              }
            }
          })
      }
    },
    prev() {
      this.isSaves('step4')
    },
    mergingProcessing() {
      this.transferListData = []
      for (let item of this.editableTabsList) {
        if (
          this.editableTabsList[this.currentTabName].suspectId !==
          item.suspectId
        ) {
          this.transferListData.push({
            key: item.suspectId,
            label: item.title,
            disabled: false
          })
        }
      }
      this.dialogVisible = true
    },
    changeBasicInfo() {
      this.isSave = true
    },
    getMergingProcessing() {
      if (this.transferVal) {
        this.dialogVisible = false
        this.isSave = true
        mergingProcessing({
          sourceSuspectInfoId:
            this.editableTabsList[this.currentTabName].suspectId + '',
          destSuspectInfoIds: this.transferVal.join(','),
          caseNumber: this.caseObj.caseNumber
        }).then(res => {
          if (res.data.code === '200') {
            this.$message({
              type: 'success',
              message: `合并成功`
            })
            this.transferVal = []
            res.data.data &&
              res.data.data.forEach((value, index) => {
                this.getGistByOptionId(value, index)
              })
          } else {
            this.$message({
              type: 'warning',
              message: res.data.msg
            })
          }
        })
      }
    },
    handleClose(done) {
      this.transferVal = []
      done()
    }
  }
}
</script>
<style lang="scss" scoped>
.addBtn {
  position: absolute;
  right: 15px;
  top: 7px;
}
.step5-no-result {
  position: relative;
  height: calc(100% - 64px);
  widows: 100%;
}
.step5-no-cell {
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center;
  margin-left: -56px;
  margin-top: -51px;
}
.h3-title {
  font-size: 14px;
  color: #333;
  margin-top: 24px;
  // margin-bottom: 12px;
  clear: both;
}
.generatingConclusion-title {
  padding-bottom: 35px;
}
.generatingConclusion-text {
  float: left;
}
.generatingConclusion {
  float: right;
  margin-top: -15px;
}
.block-wrap {
  // padding: 10px;
  margin-top: -5px;
  .item-wrap {
    // padding: 10px;
    margin-top: 16px;
  }
}
</style>

<style lang="scss">
.step5-css .el-tabs__header {
  padding-left: 24px !important;
}
.step5-css .el-tabs__nav-wrap {
  width: calc(100% - 140px) !important;
}
.step-body-wrap-step5 {
  padding: 0px 15px 15px 24px !important;
}
.step5-css .el-tabs__nav-wrap .el-tabs__item {
  font-weight: 600;
}
.step5-css .step-5-checkbox .el-checkbox {
  width: 100%;
}
.step5-css .step-5-checkbox .el-checkbox__label {
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  vertical-align: top;
  white-space: nowrap;
}
.step5-css .el-radio {
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  vertical-align: top;
  white-space: nowrap;
}
// 这个代码应该放到全局 default.scss中去，因目前只有这个页面用到了transfer，先临时放到这里
// 穿梭框中 左右栏标题 右侧的统计数据 0/0 无法通过配置删除，采用css隐藏
.yofc-transfer {
  .el-transfer-panel__header {
    .el-checkbox__label {
      font-size: 14px !important;
      span {
        display: none !important;
      }
    }
  }

  .el-transfer__buttons {
    .el-transfer__button {
      border-radius: 3px !important;
      padding: 4px 8px !important;
      i {
        font-size: 20px !important;
      }
    }
  }
}
</style>



